@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background-color: #f6f8fc;
}

img {
  max-height: 100%;
  max-width: 100%;
}

.pokemon-about__description{text-align:justify}.pokemon-about__info{display:flex;text-transform:capitalize}.pokemon-about__info__category{flex:0 1 100px;font-weight:bold;color:#bbb}

.pokemon-evolutions-container{display:flex;margin-top:20px}.pokemon-evolution{text-decoration:none;text-transform:capitalize;font-weight:bold;color:#585c5f;flex:1 1;padding:10px 0;border-radius:20px;display:flex;flex-direction:column;align-items:center}.pokemon-evolution:hover{background-color:#dadada}.pokemon-evolution__picture{margin:0 0 -28px 0}.pokemon-evolution__picture img{position:relative;z-index:1;width:100%}.pokemon-evolution__level{display:flex;justify-content:center;align-items:center;font-size:12px;border-radius:50%;width:30px;height:30px;color:#fff;position:relative;left:30px}

.spinner-container{display:flex;align-items:center;justify-content:center;width:100%;margin:50px 0}.spinner{width:100px;height:100px;background:url(/pokedex/static/media/pokeball.86b2cb90.svg);background-position:center;background-repeat:no-repeat;background-size:contain;-webkit-animation-name:animation-spinner;animation-name:animation-spinner;-webkit-animation-duration:3s;animation-duration:3s;-webkit-animation-timing-function:ease-in-out;animation-timing-function:ease-in-out;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@-webkit-keyframes animation-spinner{0%{transform:rotate(0deg)}80%{transform:rotate(1440deg)}100%{transform:rotate(1440deg)}}@keyframes animation-spinner{0%{transform:rotate(0deg)}80%{transform:rotate(1440deg)}100%{transform:rotate(1440deg)}}

.pokemon-stats__info{display:flex;text-transform:capitalize;align-items:center;padding:10px 0}.pokemon-stats__info__category{flex:0 1 105px;font-weight:bold;color:#bbb}.pokemon-stats__info__stat{text-align:center;flex:0 0 40px}.pokemon-stats__info__stat-bar{flex:1 1 auto;background-color:#f8f8f8;height:3px}.pokemon-stats__info__stat-bar--progress{height:100%}

.pokemon-moves-header__name{text-transform:capitalize}.pokemon-moves-list{list-style-position:outside;list-style-image:url(data:image/png;base64,)}.pokemon-moves-list__item{text-transform:capitalize;padding-left:10px}

.pokemon__header{padding:20px;border-radius:20px;margin-bottom:-50px;background-image:url(/pokedex/static/media/pokeball-white.1d367c3a.svg);background-repeat:no-repeat;background-position:170px 90px;background-size:200px}.pokemon-page__types-container{font-size:0.8em;font-weight:bold;color:#fff}.pokemon-page__types-container__type{text-transform:uppercase;background-color:rgba(255,255,255,0.3);border-radius:15px;margin:2px;padding:5px 15px;text-align:center}.pokemon-page__name{text-transform:capitalize;color:#fff;font-size:1.8em;padding:5px 15px}.pokemon-page__id{color:#fff;opacity:0.5;float:right;max-height:85vh}.pokemon-page__img-container{margin:0;display:flex;align-items:center;justify-content:center;position:relative;z-index:1}.pokemon-info__content{padding:20px;background-color:inherit;border-radius:20px;font-size:0.8em}.pokemon-info__navigation{display:flex;justify-content:space-evenly;border-bottom:2px solid #f8f8f8}.pokemon-info__navigation__link{padding:10px 5px;text-decoration:none;font-weight:bold;color:#bbb}.pokemon-info__navigation__link--selected{color:#585c5f;box-shadow:0 2px 0px 0px #777da8}

.pokemon-page{width:350px;margin:0 auto;border-radius:20px;box-shadow:0px 19px 16px 6px #cdd0d8;position:relative;min-height:85vh;background-color:#fff}.back-arrow{color:#fff;text-decoration:none;font-size:2em;position:absolute;top:0;left:10px;background:none;border:none;cursor:pointer}

.pokedex{margin:0 auto;max-width:85vw;min-height:85vh;padding:20px;border-radius:20px;background-color:#fff;box-shadow:0px 19px 16px 6px #cdd0d8}.pokemon-list{display:flex;justify-content:center;flex-wrap:wrap}.pokemon-list__item{text-decoration:none}@media (max-width: 700px){.pokedex{padding:10px}}

.list-item-container{height:90%;min-height:125px;width:180px;padding:5px 15px;margin:5px;border-radius:25px;cursor:pointer;transition:0.2s;color:rgba(255,255,255,0.9);background-image:url(/pokedex/static/media/pokeball-white.1d367c3a.svg);background-repeat:no-repeat;background-position:95px 40px;background-size:100px}.list-item-container:hover{transform:scale(1.02)}.name{margin:15px 0 0 0;font-size:1em;text-transform:capitalize;height:2em}.id{float:right;opacity:0.5;display:none}.info{display:flex;flex-direction:row-reverse;align-items:center;justify-content:center}.pokemon-img-container{width:50%;margin:0}.pokemon-img-container__img{object-fit:cover;object-position:center}.types-container{flex:1 1;min-width:50%;font-size:0.7em;display:flex;flex-direction:column;font-weight:bold}.types-container__type{text-transform:uppercase;background-color:rgba(255,255,255,0.3);border-radius:15px;margin:2px;padding:2px;text-align:center}@media (max-width: 460px){.list-item-container{width:250px}}@media (max-width: 320px){.list-item-container{width:auto}}

.pagination__link{color:#000;cursor:pointer;width:48px;height:48px;display:flex;align-items:center;justify-content:center;border-radius:5px;font-family:inherit;border:none;background-color:#fff}.pagination__link:hover{background-color:#dadada}.pagination__link--previous,.pagination__link--next{background-color:#515558;color:#fff;position:relative}.pagination__link--previous::before,.pagination__link--next::before{font-weight:bold;font-size:14px}.pagination__link--previous{margin-right:30px}.pagination__link--previous::before{content:"PREV"}.pagination__link--next{margin-left:30px}.pagination__link--next::before{content:"NEXT"}.pagination__link--current{color:#fff;background-color:#e7444b;font-weight:bold}.pagination__link--start,.pagination__link--end{position:relative}.pagination__link--start::after,.pagination__link--end::after{content:"...";pointer-events:none;position:absolute;display:flex;justify-content:center;align-items:center;width:100%;color:#515558;opacity:0.3;font-weight:bold;font-size:30px;top:-5px}.pagination__link--start{margin-right:48px}.pagination__link--start::after{left:100%}.pagination__link--end{margin-left:48px}.pagination__link--end::after{right:100%}@media (max-width: 700px){.pagination__link--previous{margin-right:10px}.pagination__link--next{margin-left:10px}.pagination__link--start,.pagination__link--end{margin:0}.pagination__link--start::after,.pagination__link--end::after{font-size:20px;top:5px}.pagination__link--start::after{left:25px}.pagination__link--end::after{right:25px}}@media (max-width: 445px){.pagination__link--previous,.pagination__link--next{margin:0px;width:24px}.pagination__link--previous::before{content:"<"}.pagination__link--next::before{content:">"}.pagination__link--start::after,.pagination__link--end::after{content:"."}}

.pagination{display:flex;align-items:center;justify-content:center}

.filter-form{font-size:0.8em;margin-bottom:2em}.filter-form--type{display:flex;align-items:center;justify-content:space-evenly}.filter__label{font-weight:bold;text-transform:uppercase;color:#585c5f;flex:0 1 125px}.filter__select{flex:1 1;color:#444;font-weight:bold;text-transform:capitalize;border:1px solid #e5e5e5;border-radius:5px;padding:5px 10px;background-image:linear-gradient(to bottom, #fff 0%, #e5e5e5 100%)}.filter__select:hover{border-color:#888}

.content{max-width:800px;margin:40px auto;font-family:"Open Sans", sans-serif;color:#323c46}

